.dashboardItems{
    border-radius: 15px;
    /* margin: auto; */
    margin: 20px;
    padding: 50px 20px;
    display: inline-flex;
    width: 300px;
    height: 300px;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border: 2px solid black;
}
.dashboardItems img{
    border-radius: 10px;
    width: 90%;
}
.dashboardItems button{
    font-weight: bold;
    background-color: coral;
    padding: 5px;
    margin: 10px;
    border-radius: 3px;
    box-shadow: 0px 0px 3px 1px black;

}
.dashboardItems button:hover{
    background-color: rgb(255, 96, 38);
    cursor: pointer;

}
.cartItems{
    display: flex;
    border: 2px solid rgb(0, 110, 255);
    margin: 10px;
    padding: 5px;
    border-radius: 7px;
}
.cartItems img{
    border-radius: 3px;
    width: 120px;
}
.cartItems h3{
    margin: 5px;
    flex: 1;
}
.cartItems button{
    font-weight: bold;
    border-radius: 5px;
    background-color: rgb(250, 3, 3);
    height: 2rem;
    margin: auto;
}
.cartItems button:hover{
    background-color: crimson;
    cursor: pointer;

}