.gmaillogin{
    border-radius: 15px;
    margin: auto;
    padding: 50px 20px;
    display: flex;
    width: 350px;
    flex-direction: column;
    align-items: center;
    border: 2px solid black;
}
.gmailloginButton{
    width: 100px;
justify-content: center;

}
.facebookButton,.gmailButton{
    font-weight: bolder;
    box-shadow: 0px 0px 3px 1px black;
    color: rgb(243, 243, 243);
    text-decoration: none;
    border-radius: 5px;
    padding: 8px;
    margin: 30px;

}
.gmailButton{
    background-color: rgb(250, 0, 0);

}
.facebookButton{
    background-color: rgba(41, 141, 255, 0.89);
   

}
.facebookButton:hover{
    background-color: rgba(0, 117, 250, 0.89);;
   
}
.gmailButton:hover{
    background-color: rgb(223, 1, 1);

}
