.base{
    border-radius: 15px;
    margin: auto;
    padding: 50px 20px;
    display: flex;
    width: 350px;
    flex-direction: column;
    align-items: center;
    border: 2px solid black;
    text-align: center;
}
.base button{
    background-color: rgb(135, 212, 222);
    font-weight: lighter;
    box-shadow: 0px 0px 1px 0px black;
    border: none;

}
.base input,.base button{
    margin: 10px;
    padding: 5px;
    border-radius: 3px;

}
.base button:hover{
    
    background-color: rgb(135, 180, 222);
}